.contacts-team {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    padding: 60px 0 50px 60px;
    display: flex;
    align-items: flex-end;

    @media (max-width: 480px) {
      padding: 40px 20px;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 35px;
    line-height: 38px;

    @media (max-width: 480px) {
      font-size: 26px;
      line-height: 28px;
    }

    &_red {
      color: var(--color-red);

      br {
        display: none;

        @media (max-width: 480px) {
          display: block;
        }
      }
    }
  }

  &-info {
    position: relative;
    max-width: 1000px;
    margin-right: 20px;

    @media (max-width: 480px) {
      max-width: none;
      margin: 0 0 34px 0;
    }

    &::before {
      content: '';
      position: absolute;
      top: 20px;
      right: -30px;
      width: 90px;
      height: 90px;
      background-image: url("data:image/svg+xml,%3Csvg width='90' height='90' viewBox='0 0 90 90' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_614_2515)'%3E%3Cpath d='M83.9013 54.1142C84.3192 54.2167 84.742 53.961 84.8454 53.5428L86.5319 46.7297C86.6354 46.3117 86.3804 45.8897 85.9625 45.7871C85.5447 45.6846 85.122 45.9404 85.0184 46.3585L83.5193 52.4146L77.4652 50.9294C77.0473 50.8268 76.6246 51.0827 76.5213 51.5009C76.4177 51.919 76.6727 52.341 77.0906 52.4435L83.9013 54.1142ZM2.13978 41.3456C9.07599 35.8726 23.2993 28.79 38.8404 28.1859C54.3152 27.5845 71.1214 33.3982 83.4212 53.7613L84.7563 52.9533C72.1378 32.0624 54.7723 26.0068 38.7832 26.6282C22.8601 27.2471 8.32696 34.4799 1.17549 40.1228L2.13978 41.3456Z' fill='%23E9503E'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_614_2515'%3E%3Crect width='66.4441' height='59.5706' fill='white' transform='matrix(0.670709 0.741721 0.741721 -0.670709 0.84375 40.1678)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E ");
      background-size: contain;
      background-repeat: no-repeat;

      @media (max-width: 480px) {
        top: initial;
        right: 0;
        bottom: -50px;
        width: 66px;
        height: 59px;
        background-image: url("data:image/svg+xml,%3Csvg width='81' height='77' viewBox='0 0 81 77' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_741_6309)'%3E%3Cpath d='M15.3772 69.0163C15.1051 69.3496 15.1544 69.8413 15.4876 70.1143L20.9161 74.5634C21.2491 74.8364 21.7397 74.7874 22.0118 74.4541C22.2839 74.1207 22.2344 73.6291 21.9014 73.3561L17.0761 69.4013L21.0174 64.5718C21.2895 64.2385 21.24 63.7469 20.9067 63.4739C20.5737 63.2009 20.083 63.2499 19.8109 63.5832L15.3772 69.0163ZM62.0234 0.663435C63.987 9.27793 64.2744 25.1645 58.1453 39.4587C52.0422 53.6919 39.5739 66.3723 15.902 68.7343L16.0583 70.287C40.3438 67.8638 53.2706 54.7823 59.5765 40.076C65.8564 25.4305 65.5664 9.19964 63.5419 0.317807L62.0234 0.663435Z' fill='%23E9503E'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_741_6309'%3E%3Crect width='66.4441' height='59.5706' fill='white' transform='matrix(-0.957888 0.287143 0.287143 0.957888 63.6445 0)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E ");
      }
    }

    &__descr {
      font-size: 18px;
      font-weight: 400;
      line-height: 25px;
      color: var(--color-black);
    }
  }

  &-button {
    margin-bottom: 8px;

    @media (max-width: 480px) {
      margin: 0;
    }
  }
}