@charset "UTF-8";
@font-face {
  font-family: "Euclid Flex";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/EuclidFlex-Regular.ttf") format("ttf");
  font-display: swap;
}
@font-face {
  font-family: "Euclid Flex";
  font-style: normal;
  font-weight: 500;
  src: url("../resources/fonts/EuclidFlex-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Euclid Flex";
  font-style: normal;
  font-weight: 700;
  src: url("../resources/fonts/EuclidFlex-Bold.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: var(--color-white);
  font-family: var(--font-family);
  font-feature-settings: "ss12" on;
  color: var(--color-black);
  overflow-x: hidden;
}
body.overflow-hidden {
  overflow: hidden;
}

main.overflow-hidden {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.container {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .container {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1140px;
    border-left: 1px solid var(--color-black);
    border-right: 1px solid var(--color-black);
  }
}
@media (min-width: 1499.98px) {
  .container {
    width: var(--content-width);
    border-left: 1px solid var(--color-black);
    border-right: 1px solid var(--color-black);
  }
}

.title {
  font-size: 70px;
  font-weight: 700;
  line-height: 84px;
  color: var(--color-black);
  text-transform: uppercase;
}
.title::before {
  position: absolute;
  font-size: 80px;
  font-weight: 700;
  line-height: 96px;
  color: transparent;
  z-index: -1;
}
@media (max-width: 720px) {
  .title {
    font-size: 35px;
    line-height: 42px;
  }
  .title::before {
    font-size: 45px;
    line-height: 54px;
  }
}

:root {
  --font-family: "Euclid Flex", sans-serif;
  --content-width: 1500px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-white: #EDE9E0;
  --color-black: #2A2A2A;
  --color-green: #7BBC48;
  --color-dark-green: #00A481;
  --color-red: #E9503E;
  --color-blue: #179FDB;
  --color-violet: #A3358C;
  --color-pink: #F0858F;
  --color-yellow: #FDC844;
  --color-orange: #E37329;
  --grid-gap: 30px;
}

.navbar {
  border-bottom: 1px solid var(--color-black);
}
@media (max-width: 1499.98px) {
  .navbar {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 360px;
    width: 100%;
    height: 100%;
    display: none;
    background: var(--color-white);
    border-right: 1px solid var(--color-black);
    border-bottom: none;
    transition: all 0.3s ease-in-out;
    z-index: 15;
  }
}
@media (max-width: 1499.98px) and (max-width: 500px) {
  .navbar {
    max-width: none;
  }
}
@media (max-width: 1499.98px) {
  .navbar.is-active {
    display: block;
    animation: smoothVisible 0.3s ease-in-out;
  }
  .navbar .container {
    width: 100%;
    height: 100%;
  }
}
.navbar-mobile {
  display: none;
  background-image: url(../resources/images/main-bg.png);
}
@media (max-width: 1499.98px) {
  .navbar-mobile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid var(--color-black);
  }
}
.navbar-mobile__logo {
  display: block;
}
.navbar-mobile__logo img {
  max-width: 120px;
  height: 38px;
}
.navbar-mobile__phone {
  font-size: 15px;
  font-weight: 500;
  line-height: 19px;
  color: var(--color-black);
}
.navbar-mobile__close {
  position: relative;
  width: 38px;
  height: 38px;
  cursor: pointer;
}
.navbar-mobile__close::before, .navbar-mobile__close::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-black);
}
.navbar-mobile__close::before {
  top: 18px;
  transform: rotate(45deg);
}
.navbar-mobile__close::after {
  top: 18px;
  transform: rotate(-45deg);
}
.navbar-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 60px;
}
@media (max-width: 1499.98px) {
  .navbar-wrapper {
    height: calc(100% - 82px);
    flex-direction: column;
    align-items: flex-start;
    padding-right: 0;
  }
}
.navbar-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-right: 1px solid var(--color-black);
}
.navbar-list.hide {
  display: none;
}
.navbar-list_two {
  display: none;
}
.navbar-list_two.is-open {
  display: block;
}
@media (max-width: 1499.98px) {
  .navbar-list {
    width: 100%;
    flex-direction: column;
    border-right: none;
    padding: 20px 20px 0 20px;
  }
}
.navbar-item {
  width: 160px;
  margin: 0 auto;
  text-align: center;
  padding: 24px 0;
}
@media (max-width: 1499.98px) {
  .navbar-item {
    width: 100%;
    margin: 0;
    padding: 20px 0;
    text-align: left;
  }
}
@media (max-width: 720px) {
  .navbar-item {
    padding: 15px 0;
  }
}
.navbar-item_select {
  position: relative;
}
.navbar-item_select:hover .navbar-select__body {
  display: block;
}
@media (max-width: 1499.98px) {
  .navbar-item_select:hover .navbar-select__body {
    display: none;
  }
}
.navbar-item__back {
  position: relative;
  padding-left: 34px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}
.navbar-item__back::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 12H18M6 12L11 7M6 12L11 17' stroke='%23E9503E' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}
.navbar-item__heading {
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  color: var(--color-black);
  text-transform: uppercase;
}
.navbar-item_pd-small {
  padding: 10px 0;
}
.navbar-item_hidden {
  position: relative;
  padding-left: 34px;
  display: none;
}
@media (max-width: 1499.98px) {
  .navbar-item_hidden {
    display: block;
  }
}
.navbar-item__link {
  display: block;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: var(--color-black);
}
@media (max-width: 1499.98px) {
  .navbar-item__link br {
    display: none;
  }
}
.navbar-item__link_search.is-open .form__search {
  display: block;
  height: 24px;
  padding-left: 0;
}
.navbar-item__link_search.is-open .navbar-item__link_text {
  display: none;
}
.navbar-item__link_open-icon {
  position: absolute;
  top: 10px;
  left: 0;
  width: 24px;
  height: 24px;
}
.navbar-social {
  display: flex;
  align-items: center;
}
@media (max-width: 1499.98px) {
  .navbar-social {
    width: 100%;
    justify-content: flex-start;
    border-top: 1px solid var(--color-black);
  }
}
@media (max-width: 500px) {
  .navbar-social {
    max-width: 360px;
    width: 100%;
    border: 1px solid var(--color-black);
    margin-left: 0;
  }
}
@media (max-width: 360.98px) {
  .navbar-social {
    border-right: none;
    border-left: none;
  }
}
.navbar-social__link {
  position: relative;
  display: block;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: var(--color-black);
}
.navbar-social__link_vk {
  margin-right: 34px;
  padding-left: 44px;
}
@media (max-width: 1499.98px) {
  .navbar-social__link_vk {
    margin-right: 0;
    padding: 20px 29px 20px 73px;
    border-right: 1px solid var(--color-black);
  }
}
.navbar-social__link_vk::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 29px;
  height: 19px;
  background-image: url("data:image/svg+xml,%3Csvg width='29' height='19' viewBox='0 0 29 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.795 19C5.88686 19 0.235478 11.8679 0 0H4.96315C5.12617 8.71071 8.78504 12.4004 11.6832 13.1612V0H16.3567V7.51251C19.2187 7.18919 22.2252 3.76577 23.2396 0H27.913C27.1341 4.64064 23.8737 8.06406 21.5551 9.47147C23.8737 10.6126 27.5871 13.5986 29 19H23.8555C22.7506 15.3864 19.9976 12.5906 16.3567 12.2102V19H15.795Z' fill='%23E9503E'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}
@media (max-width: 1499.98px) {
  .navbar-social__link_vk::before {
    top: 19px;
    left: 29px;
  }
}
.navbar-social__link_telegram {
  padding-left: 40px;
}
@media (max-width: 1499.98px) {
  .navbar-social__link_telegram {
    padding: 20px 32px 20px 75px;
  }
}
.navbar-social__link_telegram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 20px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='20' viewBox='0 0 24 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.64987 8.60987C8.09231 5.78741 12.3883 3.92667 14.5378 3.02765C20.675 0.460774 21.9503 0.0148807 22.7815 0.000156987C22.9643 -0.00308132 23.3731 0.0424773 23.6379 0.25852C23.8614 0.440943 23.9229 0.687369 23.9524 0.860326C23.9818 1.03328 24.0185 1.42728 23.9893 1.73514C23.6568 5.249 22.2177 13.7762 21.4856 17.7118C21.1758 19.3771 20.5658 19.9354 19.9753 19.9901C18.6919 20.1088 17.7174 19.1372 16.4744 18.3179C14.5294 17.0358 13.4305 16.2377 11.5425 14.9867C9.36064 13.5408 10.7751 12.7462 12.0185 11.4475C12.344 11.1076 17.9985 5.93584 18.1079 5.46668C18.1216 5.40801 18.1343 5.18929 18.0051 5.0738C17.8759 4.95831 17.6851 4.9978 17.5475 5.02921C17.3524 5.07373 14.2452 7.1389 8.22584 11.2247C7.34386 11.8337 6.545 12.1304 5.82924 12.1149C5.04018 12.0977 3.52233 11.6663 2.39397 11.2974C1.00999 10.8451 -0.0899676 10.6059 0.00581423 9.83761C0.0557033 9.43745 0.603723 9.0282 1.64987 8.60987Z' fill='%23E9503E'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}
@media (max-width: 1499.98px) {
  .navbar-social__link_telegram::before {
    top: 19px;
    left: 35px;
  }
}
.navbar-select__header {
  cursor: pointer;
}
.navbar-select__selected {
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: var(--color-black);
}
@media (max-width: 1499.98px) {
  .navbar-select__selected br {
    display: none;
  }
}
.navbar-select__body {
  position: absolute;
  bottom: -120px;
  left: 0;
  display: none;
  width: 270px;
  height: 120px;
  background: var(--color-white);
  z-index: 10;
}
.navbar-select__body.is-open {
  display: block;
}
.navbar-select__list {
  border: 1px solid var(--color-black);
}
.navbar-select__item {
  padding: 20px;
  text-align: left;
}
.navbar-select__item:not(:last-child) {
  border-bottom: 1px solid var(--color-black);
}
.navbar-select__link {
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: var(--color-black);
}

@keyframes smoothVisible {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.btn {
  display: flex;
  align-items: center;
  height: 60px;
  padding: 18px 51px 20px 51px;
  border: 1px solid var(--color-black);
  border-radius: 30px;
  overflow: hidden;
}
.btn:hover .btn__text:nth-child(odd) {
  transform: translateY(0);
}
.btn:hover .btn__text:nth-child(even) {
  transform: translateY(0);
}
.btn__text {
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
  color: var(--color-black);
  text-transform: uppercase;
  transition: 0.5s;
}
.btn__text:nth-child(odd) {
  transform: translateY(-250%);
}
.btn__text:nth-child(odd)::before {
  content: attr(data-text);
  position: absolute;
  transform: translateY(250%);
}
.btn__text:nth-child(even) {
  transform: translateY(250%);
}
.btn__text:nth-child(even)::before {
  content: attr(data-text);
  position: absolute;
  transform: translateY(-250%);
}
.btn__icon {
  display: block;
  margin-left: 15px;
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 4V6.4C4 9.76032 4 11.4405 4.65396 12.7239C5.2292 13.8529 6.14708 14.7708 7.27606 15.346C8.55953 16 10.2397 16 13.6 16H20M20 16L16 12M20 16L16 20' stroke='%232A2A2A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}

.swiper__button {
  position: absolute;
  width: 60px;
  height: 60px;
  border: 1px solid var(--color-black);
  border-radius: 100%;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 1;
}
@media (max-width: 720px) {
  .swiper__button {
    width: 40px;
    height: 40px;
  }
}
.swiper__button_prev {
  left: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18 12L6 12M6 12L11 17M6 12L11 7' stroke='%232A2A2A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
}
.swiper__button_next {
  right: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 12H18M18 12L13 7M18 12L13 17' stroke='%232A2A2A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
}
.swiper__button_disabled {
  display: none;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: none;
  background: rgba(237, 233, 224, 0.2);
  backdrop-filter: blur(10px);
  z-index: 10;
}
.overlay.is-active {
  display: block;
}

.breadcrubms {
  position: absolute;
  top: 40px;
  width: auto;
}
@media (max-width: 480px) {
  .breadcrubms {
    position: initial;
    margin-bottom: 35px;
  }
}
.breadcrubms-list {
  display: flex;
  white-space: nowrap;
}
@media (max-width: 480px) {
  .breadcrubms-list {
    overflow-x: scroll;
    overflow-y: hidden;
  }
}
.breadcrubms-item {
  position: relative;
}
.breadcrubms-item:not(:last-child) {
  margin-right: 5px;
  padding-right: 20px;
}
.breadcrubms-item:not(:last-child)::before {
  content: "";
  position: absolute;
  top: 3px;
  right: 0;
  width: 15px;
  height: 15px;
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.625 3.75L9.375 7.5L5.625 11.25' stroke='%232A2A2A' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
}
.breadcrubms-item__link {
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: var(--color-black);
}

@media (max-width: 480px) {
  .form {
    max-width: 320px;
    margin: 0 auto;
  }
}
.form-wrapper {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
@media (max-width: 480px) {
  .form-wrapper {
    flex-direction: column;
  }
}
.form-item {
  width: 100%;
}
.form-item_md {
  width: 33%;
}
@media (max-width: 480px) {
  .form-item_md {
    width: 100%;
  }
}
.form-item_lg {
  width: 49%;
}
@media (max-width: 480px) {
  .form-item_lg {
    width: 100%;
  }
}
.form-item_icon-person {
  position: relative;
}
.form-item_icon-person::before {
  content: "";
  position: absolute;
  top: 20px;
  right: 20px;
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13 20V18C13 15.2386 10.7614 13 8 13C5.23858 13 3 15.2386 3 18V20H13ZM13 20H21V19C21 16.0545 18.7614 14 16 14C14.5867 14 13.3103 14.6255 12.4009 15.6311M11 7C11 8.65685 9.65685 10 8 10C6.34315 10 5 8.65685 5 7C5 5.34315 6.34315 4 8 4C9.65685 4 11 5.34315 11 7ZM18 9C18 10.1046 17.1046 11 16 11C14.8954 11 14 10.1046 14 9C14 7.89543 14.8954 7 16 7C17.1046 7 18 7.89543 18 9Z' stroke='%23E37329' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
}
.form__input {
  width: 100%;
  height: 60px;
  padding: 19px 20px 19px 20px;
  background: transparent;
  border: 1px solid var(--color-black);
}
.form__input_date::-webkit-calendar-picker-indicator {
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3 10H21M7 3V5M17 3V5M6.2 21H17.8C18.9201 21 19.4802 21 19.908 20.782C20.2843 20.5903 20.5903 20.2843 20.782 19.908C21 19.4802 21 18.9201 21 17.8V8.2C21 7.07989 21 6.51984 20.782 6.09202C20.5903 5.71569 20.2843 5.40973 19.908 5.21799C19.4802 5 18.9201 5 17.8 5H6.2C5.0799 5 4.51984 5 4.09202 5.21799C3.71569 5.40973 3.40973 5.71569 3.21799 6.09202C3 6.51984 3 7.07989 3 8.2V17.8C3 18.9201 3 19.4802 3.21799 19.908C3.40973 20.2843 3.71569 20.5903 4.09202 20.782C4.51984 21 5.07989 21 6.2 21Z' stroke='%23E37329' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
  background-repeat: no-repeat;
  cursor: pointer;
}
.form__input_time::-webkit-calendar-picker-indicator {
