.organizations-hero {
  background-image: url(../resources/images/main-bg.png);
  border-bottom: 1px solid var(--color-black);

  @media (max-width: 480px) {
    border-bottom: none;
  }

  &-wrapper {
    display: flex;

    @media (max-width: 480px) {
      display: block;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 50px;
    line-height: 55px;

    @media (max-width: 480px) {
      margin-bottom: 20px;
      font-size: 26px;
      line-height: 28px;
    }
  }

  &__descr {
    margin-bottom: 35px;
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: var(--color-black);

    @media (max-width: 480px) {
      margin-bottom: 20px;
    }
  }

  &-left {
    position: relative;
    padding: 90px 50px 50px 60px;

    @media (max-width: 480px) {
      padding: 20px 20px;
    }

    &__inner {
      max-width: 810px;
      width: 100%;

      @media (max-width: 480px) {
        max-width: none;
      }
    }

    &__count {
      display: block;
      margin-top: 15px;
      font-size: 16px;
      font-weight: 700;
      line-height: 16px;
      color: var(--color-black);
      text-transform: uppercase;

      @media (max-width: 480px) {
        margin-bottom: 5px;
      }

      &_big {
        display: block;
        font-size: 96px;
        font-weight: 700;
        line-height: 96px;
        color: var(--color-red);

        @media (max-width: 480px) {
          font-size: 64px;
          line-height: 64px;
        }
      }
    }
  }

  &-right {
    position: relative;

    @media (max-width: 480px) {
      height: 340px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../resources/images/main-bg.png);
    }

    &__play {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 115px;
      height: 112px;
      background-image: url(../resources/images/icons/play.svg);
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
      cursor: pointer;
    }

    &__click {
      position: absolute;
      left: 50%;
      bottom: 160px;
      transform: translateX(-50%) rotate(-6.36deg);
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      letter-spacing: -0.01em;
      text-align: center;
      color: var(--color-white);

      @media (max-width: 480px) {
        bottom: 50px;
        width: 285px;
      }

      &::before {
        content: '';
        position: absolute;
        top: -80px;
        left: 50px;
        width: 31px;
        height: 63px;
        background-image: url("data:image/svg+xml,%3Csvg width='20' height='71' viewBox='0 0 20 71' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19.8912 2.07069C19.9302 1.79731 19.7402 1.54408 19.4669 1.50506L15.012 0.869321C14.7386 0.830312 14.4854 1.02029 14.4464 1.29367C14.4074 1.56704 14.5974 1.82028 14.8707 1.85929L18.8306 2.4244L18.2655 6.38428C18.2265 6.65765 18.4165 6.91089 18.6898 6.9499C18.9632 6.98891 19.2165 6.79893 19.2555 6.52555L19.8912 2.07069ZM16.0998 70.3056C11.0304 65.0538 4.04986 53.9506 2.28719 41.2207C0.530338 28.5327 3.95006 14.2133 19.6963 2.4L19.0962 1.60009C3.03331 13.6509 -0.505483 28.3429 1.29664 41.3578C3.09295 54.3307 10.1889 65.6219 15.3803 71.0001L16.0998 70.3056Z' fill='%23EDE9E0'/%3E%3C/svg%3E ");
        background-repeat: no-repeat;

        @media (max-width: 480px) {
          left: 60px;
        }
      }
    }

    img {
      height: 100%;

      @media (max-width: 480px) {
        width: 100%;
      }
    }
  }
}