.capabilities-main {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    padding: 60px;
    display: flex;
  }

  &-left {
    margin-right: 50px;
  }

  &-tabs {
    &__btn {
      position: relative;
      font-feature-settings: "ss12" on;
      padding: 21px 50px 17px 40px;

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &_count {
        position: absolute;
        top: 17px;
        right: 40px;
        font-size: 13px;
        font-weight: 500;
        font-feature-settings: "ss12" on;
        color: var(--color-red);
      }

      &_active {
        background: var(--color-red);
        border: 1px solid var(--color-red);
        color: var(--color-white);

        .capabilities-main-tabs__btn_count {
          color: var(--color-white);
        }
      }
    }
  }

  &-right {
    max-width: 1040px;

    &__list {
      display: none;
      flex-wrap: wrap;
      gap: 40px;

      &_active {
        display: flex;
      }
    }
  }

  &-item {
    max-width: 320px;
    width: 100%;

    &__header {
      position: relative;
      margin-bottom: 16px;
    }

    &__tab {
      position: absolute;
      top: 0;
      left: 0;
      padding: 5px 10px;
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
      text-transform: uppercase;

      &_pink {
        background: var(--color-pink);
      }

      &_green {
        background: var(--color-green);
      }

      &_orange {
        background: var(--color-orange);
      }
    }

    &__title {
      margin-bottom: 5px;
      font-size: 20px;
      font-weight: 700;
      line-height: 24px;
      color: var(--color-black);
    }

    &__descr {
      margin-bottom: 16px;
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      color: var(--color-black);
    }

    &__link {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }
  }

  &-inner {
    height: 61px;
    padding: 0 60px;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--color-black);
  }

  &-pagination {
    display: flex;

    @media (max-width: 480px) {
      width: 100%;
      justify-content: space-between;
    }

    &__btn {
      display: block;
      width: 60px;
      height: 60px;
      border-right: 1px solid var(--color-black);
      font-size: 18px;
      font-feature-settings: 'ss12' on;
      font-weight: 400;
      line-height: 25px;
      color: var(--color-black);

      &:first-child {
        border-left: 1px solid var(--color-black);

        @media (max-width: 480px) {
          border-left: none;
        }
      }

      &:not(:first-child) {
        @media (max-width: 480px) {
          border-left: 1px solid var(--color-black);
        }
      }

      &:last-child {
        @media (max-width: 480px) {
          border-right: none;
        }
      }

      &:hover {
        color: var(--color-red);
      }

      &.is-active {
        background: var(--color-red);
        color: #fff;
      }
    }
  }

  &__show-more {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 220px;
    border-left: 1px solid var(--color-black);
    border-right: 1px solid var(--color-black);

    @media (max-width: 480px) {
      display: none;
    }

    &_text {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }

    &_icon {
      background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 4.5H6.9C10.2603 4.5 11.9405 4.5 13.2239 5.15396C14.3529 5.7292 15.2708 6.64708 15.846 7.77606C16.5 9.05953 16.5 10.7397 16.5 14.1L16.5 20.5M16.5 20.5L12.5 16.5M16.5 20.5L20.5 16.5' stroke='%232A2A2A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
    }
  }
}