.team-hero {
  background-image: url(../resources/images/main-bg.png);
  border-bottom: 1px solid var(--color-black);

  &-inner {
    position: relative;
    padding: 90px 60px 50px;

    @media (max-width: 480px) {
      padding: 20px 20px 40px;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 50px;
    line-height: 55px;
    text-align: center;

    &_red {
      color: var(--color-red);
    }

    br {
      display: none;

      @media (max-width: 480px) {
        display: block;
      }
    }

    @media (max-width: 480px) {
      max-width: 300px;
      margin-bottom: 20px;
      font-size: 26px;
      line-height: 28px;
    }
  }

  &__descr {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    text-align: center;
    color: var(--color-black);

    @media (max-width: 480px) {
      font-size: 16px;
      line-height: 22px;
    }
  }
}