.activities-projects {
  border-bottom: 1px solid var(--color-black);

  @media (max-width: 480px) {
    padding-bottom: 40px;
  }

  &-wrapper {
    display: flex;
    justify-content: space-between;

    @media (max-width: 480px) {
      display: block;
    }
  }

  &__inner {
    padding: 60px 0 60px 60px;

    @media (max-width: 480px) {
      padding: 40px 20px;
    }
  }

  &__title {
    margin-bottom: 40px;
    font-size: 35px;
    line-height: 42px;
  }

  &-buttons {
    margin-bottom: 40px;
    display: flex;

    @media (max-width: 480px) {
      white-space: nowrap;
      overflow-x: scroll;
      overflow-y: hidden;
    }

    &__btn {
      position: relative;
      padding: 21px 50px 17px 40px;

      @media (max-width: 480px) {
        overflow: visible;
      }

      &:not(:last-child) {
        margin-right: 20px;
      }

      &_count {
        position: absolute;
        top: 17px;
        right: 40px;
        font-size: 13px;
        font-weight: 500;
        font-feature-settings: "ss12" on;
        color: var(--color-red);
      }

      &_active {
        background: var(--color-red);
        border: 1px solid var(--color-red);
        color: var(--color-white);

        .activities-projects-buttons__btn_count {
          color: var(--color-white);
        }
      }
    }
  }

  &-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    @media (max-width: 480px) {
      flex-direction: column;
      flex-wrap: nowrap;
      gap: none;
    }

    &_disp-none {
      display: none!important;
    }

    &_active {
      display: flex!important;
    }
  }

  &-item {
    max-width: 420px;
    width: 100%;
    display: flex;
    flex-direction: column;

    @media (max-width: 480px) {
      max-width: 320px;
    }

    &__header {
      margin-bottom: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid var(--color-black);
    }

    &__body {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &__title {
      margin-top: 20px;
      font-size: 22px;
      font-weight: 700;
      line-height: 31px;
      color: var(--color-black);
      text-transform: uppercase;
    }

    &__descr {
      margin-bottom: 15px;
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      color: var(--color-black);
      opacity: .8;
    }

    &__btn {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }
  }

  &-all {
    width: 80px;
    background-image: url(../resources/images/main-bg.png);

    @media (max-width: 480px) {
      width: 100%;
    }
  }

  &-empty {
    width: 80px;
    background-image: url(../resources/images/main-bg.png);
    border-left: 1px solid var(--color-black);

    @media (max-width: 480px) {
      display: none;
    }
  }

  &__btn {
    height: 100%;
    border: none;
    border-left: 1px solid var(--color-black);
    border-radius: 0;
    padding: 0;
    flex-direction: column;
    justify-content: flex-end;

    @media (max-width: 480px) {
      flex-direction: row;
      justify-content: space-between;
      border-left: none;
      border-top: 1px solid var(--color-black);
      border-bottom: 1px solid var(--color-black);
    }

    &-text {
      padding: 0 29px 40px 29px;
      writing-mode: vertical-rl;
      color: var(--color-black);

      @media (max-width: 480px) {
        writing-mode: initial;
        padding: 20px;
      }
    }

    &-icon {
      display: block;
      width: 100%;
      height: 80px;
      padding: 28px;
      border-top: 1px solid var(--color-black);
      background-color: var(--color-white);
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 4V6.4C4 9.76032 4 11.4405 4.65396 12.7239C5.2292 13.8529 6.14708 14.7708 7.27606 15.346C8.55953 16 10.2397 16 13.6 16H20M20 16L16 12M20 16L16 20' stroke='%232A2A2A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
      background-size: 24px;
      background-position: center;
      background-repeat: no-repeat;

      @media (max-width: 480px) {
        width: 60px;
        height: 60px;
        border-top: none;
        border-left: 1px solid var(--color-black);
      }
    }
  }

  &__organizations-page {
    padding-bottom: 0;
    border-bottom: none;
  }
}