.project-page {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    display: flex;
    align-items: center;

    @media (max-width: 480px) {
      display: block;
    }
  }

  &-left {
    position: relative;
    padding: 90px 50px 60px 60px;

    @media (max-width: 480px) {
      padding: 20px 20px 0;
    }
  }

  &__inner {
    max-width: 630px;
  }

  &__title {
    margin-bottom: 30px;
    font-size: 50px;
    line-height: 55px;

    @media (max-width: 480px) {
      max-width: 300px;
      font-size: 26px;
      line-height: 28px;
    }
  }

  &-right {
    padding-left: 50px;

    @media (max-width: 480px) {
      padding: 0 20px 35px;
    }

    &__info {
      position: relative;
      padding: 32px 22px 42px 52px;

      @media (max-width: 480px) {
        padding: 41px 27px 39px 48px;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 557px;
        height: 198px;
        background-image: url("data:image/svg+xml,%3Csvg width='559' height='200' viewBox='0 0 559 200' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M203.5 0.5L0.5 22.5V165.5L106 198.808L558 165V39.5L203.5 0.5Z' stroke='%232A2A2A'/%3E%3C/svg%3E ");
        background-size: contain;
        background-repeat: no-repeat;

        @media (max-width: 480px) {
          width: 323px;
          height: 291px;
          background-image: url("data:image/svg+xml,%3Csvg width='325' height='293' viewBox='0 0 325 293' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M118.613 1L1 33.2832V243.124L62.1238 292L324 276.5V26L118.613 1Z' stroke='%232A2A2A'/%3E%3C/svg%3E ");
        }
      }

      &::after {
        content: '';
        position: absolute;
        top: -10px;
        left: -70px;
        width: 103px;
        height: 83px;
        background-image: url("data:image/svg+xml,%3Csvg width='113' height='96' viewBox='0 0 113 96' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M53.4619 13.4033L95.417 7.93518L99.9685 42.8574C100.802 47.9572 100.359 53.1848 98.6781 58.0725L98.6436 58.1727L98.6057 58.2715C97.034 62.3654 94.618 66.0826 91.5149 69.1812L91.5024 69.1937L91.4897 69.2062C88.5345 72.1224 85.1698 74.592 81.5019 76.5373M81.5019 76.5373C78.1584 78.3128 74.8328 79.9506 71.5267 81.4486L67.0798 83.4635L55.1374 67.0902C53.6872 69.8567 51.8288 72.3975 49.6189 74.6245L49.6031 74.6405L49.5871 74.6563C46.6445 77.5777 43.2882 80.0504 39.626 81.9951C36.2876 83.7705 32.9615 85.4084 29.6478 86.9077L25.2399 88.9021L12.0216 71.0919L18.1543 67.8108C20.0161 66.8147 21.8057 65.6891 23.51 64.4424L23.5539 64.4103L23.5984 64.379C24.2017 63.9542 24.767 63.4794 25.2885 62.9599L13.728 64.4666L7.84766 19.3482L53.4619 13.4033M57.9724 58.6599C57.6888 60.3122 57.2719 61.9445 56.7229 63.5406L56.6896 63.6373L56.6531 63.7328C56.597 63.8796 56.5397 64.0259 56.4814 64.1717L60.1146 62.2578C61.9735 61.2786 63.7627 60.1726 65.4696 58.9476L65.485 58.9365L65.5006 58.9255C66.1199 58.4869 66.6977 57.9942 67.2275 57.4536L57.9724 58.6599Z' fill='%23E9503E'/%3E%3Cpath d='M56.4065 19.0699L90.2403 14.6603L94.0296 43.7344C94.7236 47.8845 94.3703 52.1422 93.0018 56.1211C91.732 59.4287 89.7801 62.432 87.2729 64.9354C84.7321 67.4426 81.8392 69.5659 78.6855 71.2381C75.4472 72.9577 72.2347 74.5395 69.0479 75.9834L62.9086 67.5663C65.0111 66.4587 67.0349 65.2077 68.9655 63.8221C70.4622 62.7622 71.7947 61.4876 72.9198 60.0393C73.8854 58.7702 74.5618 57.3052 74.9017 55.7472C75.2402 53.9798 75.2769 52.1679 75.01 50.3882L60.7308 52.2493L56.4065 19.0699ZM14.5703 24.5225L48.2852 20.1284L52.0744 49.2025C52.7684 53.3525 52.4152 57.6102 51.0467 61.5892C49.7842 64.8896 47.8464 67.89 45.3574 70.3983C42.8323 72.9052 39.9522 75.0271 36.8096 76.6958C33.578 78.4146 30.3655 79.9964 27.1721 81.4412L20.9823 73.1012C23.0917 71.9726 25.1192 70.6974 27.05 69.285C28.5428 68.234 29.8763 66.9733 31.0095 65.5419C31.9722 64.2727 32.6453 62.8082 32.9815 61.2511C33.32 59.4837 33.3566 57.6717 33.0898 55.8921L18.8998 57.7415L14.5703 24.5225Z' fill='%23FDC844'/%3E%3C/svg%3E ");
        background-size: contain;
        background-repeat: no-repeat;

        @media (max-width: 480px) {
          top: -4px;
          left: -18px;
          width: 75px;
          height: 60px;
        }
      }
    }

    &__author {
      margin-bottom: 10px;
      font-size: 18px;
      font-weight: 700;
      line-height: 25px;
      color: var(--color-black);
    }

    &__descr {
      max-width: 480px;
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      color: var(--color-black);

      @media (max-width: 480px) {
        max-width: 250px;
      }
    }
  }
}