.our-activities {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    padding: 60px;
    display: flex;
    align-items: flex-start;

    @media (max-width: 480px) {
      padding: 20px 20px 60px;
    }
  }

  &-left {
    margin-right: 40px;

    @media (max-width: 480px) {
      display: none;
    }

    &__list {
      max-width: 340px;
      width: 100%;
      border: 1px solid var(--color-black);
    }

    &__item {
      padding: 20px;

      &:not(:last-child) {
        border-bottom: 1px solid var(--color-black);
      }
    }

    &__link {
      font-size: 18px;
      font-weight: 700;
      line-height: 20px;
      color: var(--color-black);
    }
  }

  &-right {
    max-width: 1000px;
    width: 100%;

    @media (max-width: 480px) {
      max-width: none;
    }

    &__list {
      @media (max-width: 480px) {
        max-width: 320px;
        margin: 0 auto;
      }
    }

    &__item {
      display: flex;

      @media (max-width: 480px) {
        max-width: 320px;
        width: 100%;
        flex-direction: column;
        align-items: center;
        text-align: center;
      }

      &:not(:last-child) {
        padding-bottom: 35px;
        border-bottom: 1px solid var(--color-black);

        @media (max-width: 480px) {
          padding-bottom: 0;
          border: none;
        }
      }

      &:not(:first-child) {
        margin-top: 40px;

        @media (max-width: 480px) {
          margin-top: 35px;
        }
      }
    }

    &__icon {
      margin-right: 40px;
      width: 220px;
      height: 220px;
      display: flex;
      justify-content: center;
      align-items: center;

      @media (max-width: 480px) {
        margin-right: 0;
        margin-bottom: 20px;
      }

      &_1 {
        background-color: #5975B8;
      }

      &_2 {
        background-color: var(--color-blue);
      }

      &_3 {
        background-color: var(--color-orange);
      }

      &_4 {
        background-color: var(--color-red);
      }

      &_5 {
        background-color: var(--color-dark-green);
      }

      &_6 {
        background-color: #7260A9;
      }

      &_7 {
        background-color: var(--color-violet);
      }

      &_8 {
        background-color: var(--color-green);
      }

      &_9 {
        background-color: var(--color-yellow);
      }

      &_10 {
        background-color: var(--color-pink);
      }

      &_11 {
        background-image: url(../resources/images/main-bg.png);
      }
    }

    &__info {
      max-width: 740px;
      width: 100%;

      @media (max-width: 480px) {
        max-width: none;
      }
    }

    &__name {
      margin-bottom: 20px;
      font-size: 24px;
      font-weight: 700;
      line-height: 34px;
      text-transform: uppercase;

      @media (max-width: 480px) {
        font-size: 22px;
        line-height: 30px;
      }

      &_1 {
        color: #5975B8;
      }

      &_2 {
        color: var(--color-blue);
      }

      &_3 {
        color: var(--color-orange);
      }

      &_4 {
        color: var(--color-red);
      }

      &_5 {
        color: var(--color-dark-green);
      }

      &_6 {
        color: #7260A9;
      }

      &_7 {
        color: var(--color-violet);
      }

      &_8 {
        color: var(--color-green);
      }

      &_9 {
        color: var(--color-black);
      }

      &_10 {
        color: var(--color-pink);
      }

      &_11 {
        color: var(--color-black);
      }
    }

    &__descr {
      margin-bottom: 20px;
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      color: var(--color-black);

      @media (max-width: 480px) {
        -webkit-line-clamp: 4;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }

    &__btn {
      max-width: 240px;
      width: 100%;

      @media (max-width: 480px) {
        margin: 0 auto;
      }
    }
  }
}