.stories-hero {
  background-image: url(../resources/images/main-bg.png);
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    display: flex;

    @media (max-width: 480px) {
      display: block;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 50px;
    line-height: 55px;

    @media (max-width: 480px) {
      margin-bottom: 20px;
      font-size: 26px;
      line-height: 28px;
    }
  }

  &__descr {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: var(--color-black);

    @media (max-width: 480px) {
      font-size: 16px;
      line-height: 22px;
    }
  }

  &-left {
    position: relative;
    padding: 90px 50px 0 60px;

    @media (max-width: 480px) {
      padding: 20px 20px 30px;
    }

    &__inner {
      max-width: 820px;

      @media (max-width: 480px) {
        max-width: none;
      }
    }
  }

  &-right {
    &__inner {
      padding: 60px 0 54px 50px;

      @media (max-width: 480px) {
        padding: 0 20px 40px;
      }
    }
  }
}