.team-main {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    display: flex;
    justify-content: space-between;

    @media (max-width: 480px) {
      display: block;
    }
  }

  &-inner {
    width: 100%;
    padding: 0 20px 0 60px;

    @media (max-width: 480px) {
      padding: 0 20px 60px;
    }

    &_pagination {
      display: flex;
      justify-content: space-between;
      padding: 0 0 0 60px;
      border-top: 1px solid var(--color-black);

      @media (max-width: 480px) {
        padding: 0;
      }
    }
  }

  &-sort {
    margin-bottom: 75px;
    display: flex;

    @media (max-width: 480px) {
      display: block;
      margin-bottom: 40px;
    }
  }

  &-director {
    max-width: 560px;
    width: 100%;

    @media (max-width: 480px) {
      max-width: 320px;
    }

    &__inner {
      position: relative;
      height: 400px;
      margin: 60px 40px 0 0;
      background-image: url(../resources/images/main-bg.png);

      @media (max-width: 480px) {
        margin: 50px 0 70px 0;
      }
    }

    &__img {
      position: absolute;
      top: 0;
      left: 0;
      transform: rotate(-9.46deg);

      @media (max-width: 480px) {
        left: 27px;
        width: 266px;
        height: 350px;
      }
    }

    &__info {
      position: absolute;
      top: 130px;
      right: -25px;
      width: 290px;
      height: 190px;
      padding: 20px 25px;
      background: var(--color-red);
      transform: rotate(3.23deg);

      @media (max-width: 480px) {
        top: initial;
        right: 14px;
        bottom: -100px;
      }
    }

    &__position {
      margin-bottom: 5px;
      font-size: 18px;
      font-weight: 500;
      line-height: 25px;
      color: var(--color-white);
      text-transform: uppercase;
    }

    &__name {
      margin-bottom: 10px;
      font-size: 22px;
      font-weight: 700;
      line-height: 31px;
      color: var(--color-black);
      text-transform: uppercase;
    }

    &__email {
      display: block;
      margin-bottom: 10px;
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }

    &__social {
      display: flex;
      font-size: 16px;
      font-weight: 400;
      line-height: 16px;
      color: var(--color-black);
    }

    &__link {
      display: block;
      margin-left: 15px;

      &_vk {
        width: 29px;
        height: 19px;
        background-image: url("data:image/svg+xml,%3Csvg width='31' height='22' viewBox='0 0 31 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.7612 20.4938C5.86875 19.9352 0.628481 12.4957 1.0625 0.633484L6.01775 0.913311C5.6894 9.61936 9.13442 13.5095 11.9851 14.4324L12.7272 1.2922L17.3932 1.55569L16.9697 9.05626C19.8453 8.89481 23.04 5.64634 24.2651 1.94376L28.9311 2.20725C27.8918 6.79659 24.4435 10.0307 22.0493 11.3052C24.2998 12.5752 27.8391 15.7658 28.9451 21.2383L23.8089 20.9483C22.9094 17.2781 20.3184 14.3315 16.7048 13.7465L16.322 20.5255L15.7612 20.4938Z' fill='%23EDE9E0'/%3E%3C/svg%3E ");
        background-size: contain;
        background-repeat: no-repeat;
      }

      &_telegram {
        width: 24px;
        height: 20px;
        background-image: url("data:image/svg+xml,%3Csvg width='26' height='22' viewBox='0 0 26 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.154 8.80345C9.74533 6.34871 14.1394 4.73314 16.3361 3.95675C22.6084 1.73998 23.9067 1.36669 24.7374 1.39886C24.9201 1.40593 25.3257 1.47447 25.5779 1.70509C25.7908 1.89983 25.8383 2.14933 25.858 2.32367C25.8776 2.49802 25.892 2.89346 25.8455 3.19918C25.3154 6.6887 23.3978 15.1212 22.445 19.0093C22.0418 20.6544 21.4013 21.1775 20.8086 21.1988C19.5206 21.245 18.6024 20.22 17.4076 19.3319C15.5379 17.9422 14.4859 17.0834 12.6714 15.7279C10.5745 14.1613 12.0315 13.4477 13.3462 12.2212C13.6903 11.9002 19.6273 7.05543 19.7631 6.59319C19.78 6.53538 19.8051 6.31772 19.6826 6.19513C19.5601 6.07254 19.3674 6.10121 19.2282 6.12481C19.031 6.15827 15.8122 8.04496 9.57208 11.7849C8.65717 12.3432 7.84285 12.5944 7.1291 12.5385C6.34226 12.4769 4.85116 11.9606 3.74539 11.5287C2.38912 10.999 1.30439 10.6982 1.44334 9.93655C1.51571 9.53984 2.08593 9.16214 3.154 8.80345Z' fill='%23EDE9E0'/%3E%3C/svg%3E ");
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }

  &-tabs {
    max-width: 724px;
    padding: 110px 0 65px 40px;
    display: flex;
    flex-wrap: wrap;

    @media (max-width: 480px) {
      padding: 70px 0 0;
      display: -webkit-box;
      flex-wrap: nowrap;
      white-space: nowrap;
      overflow-x: scroll;
      overflow-y: hidden;
    }

    &__btn {
      position: relative;
      font-feature-settings: "ss12" on;
      padding: 21px 50px 17px 40px;
      text-transform: uppercase;

      &:not(:last-child) {
        margin-bottom: 20px;

        @media (max-width: 480px) {
          margin-bottom: 0;
          margin-right: 20px;
        }
      }

      &:first-child {
        margin-right: 20px;
      }

      &:nth-child(2) {
        margin-right: 20px;
      }

      &_count {
        position: absolute;
        top: 17px;
        right: 40px;
        font-size: 13px;
        font-weight: 500;
        font-feature-settings: "ss12" on;
        color: var(--color-red);
      }

      &_active {
        background: var(--color-red);
        border: 1px solid var(--color-red);
        color: var(--color-white);

        .team-main-tabs__btn_count {
          color: var(--color-white);
        }
      }
    }
  }

  &-content {
    padding: 65px 0 60px 0;

    @media (max-width: 480px) {
      padding: 0;
    }
  }

  &-list {
    display: none;
    flex-wrap: wrap;
    gap: 40px 20px;

    &_active {
      display: flex;
    }

    @media (max-width: 480px) {
      gap: 30px 0;
    }
  }

  &-item {
    max-width: 420px;
    width: 100%;

    @media (max-width: 480px) {
      max-width: 320px;
      margin: 0 auto;
    }

    &__header {
      margin-bottom: 20px;
    }

    &__position {
      margin-bottom: 5px;
      font-size: 18px;
      font-weight: 500;
      line-height: 25px;
      color: var(--color-red);
      text-transform: uppercase;
    }

    &__name {
      margin-bottom: 10px;
      font-size: 22px;
      font-weight: 700;
      line-height: 31px;
      color: var(--color-black);
      text-transform: uppercase;
    }

    &__email {
      display: block;
      margin-bottom: 10px;
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }

    &__social {
      display: flex;
      font-size: 16px;
      font-weight: 400;
      line-height: 16px;
      color: var(--color-black);
    }

    &__link {
      display: block;
      margin-left: 15px;

      &_vk {
        width: 29px;
        height: 19px;
        background-image: url("data:image/svg+xml,%3Csvg width='29' height='19' viewBox='0 0 29 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.795 19C5.88686 19 0.235478 11.8679 0 0H4.96315C5.12617 8.71071 8.78504 12.4004 11.6832 13.1612V0H16.3567V7.51251C19.2187 7.18919 22.2252 3.76577 23.2396 0H27.913C27.1341 4.64064 23.8737 8.06406 21.5551 9.47147C23.8737 10.6126 27.5871 13.5986 29 19H23.8555C22.7506 15.3864 19.9976 12.5906 16.3567 12.2102V19H15.795Z' fill='%23E9503E'/%3E%3C/svg%3E ");
        background-size: contain;
        background-repeat: no-repeat;
      }

      &_telegram {
        width: 24px;
        height: 20px;
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='20' viewBox='0 0 24 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.64987 8.60987C8.09231 5.78741 12.3883 3.92667 14.5378 3.02765C20.675 0.460774 21.9503 0.0148807 22.7815 0.000156987C22.9643 -0.00308132 23.3731 0.0424773 23.6379 0.25852C23.8614 0.440943 23.9229 0.687369 23.9524 0.860326C23.9818 1.03328 24.0185 1.42728 23.9893 1.73514C23.6568 5.249 22.2177 13.7762 21.4856 17.7118C21.1758 19.3771 20.5658 19.9354 19.9753 19.9901C18.6919 20.1088 17.7174 19.1372 16.4744 18.3179C14.5294 17.0358 13.4305 16.2377 11.5425 14.9867C9.36064 13.5408 10.7751 12.7462 12.0185 11.4475C12.344 11.1076 17.9985 5.93584 18.1079 5.46668C18.1216 5.40801 18.1343 5.18929 18.0051 5.0738C17.8759 4.95831 17.6851 4.9978 17.5475 5.02921C17.3524 5.07373 14.2452 7.1389 8.22584 11.2247C7.34386 11.8337 6.545 12.1304 5.82924 12.1149C5.04018 12.0977 3.52233 11.6663 2.39397 11.2974C1.00999 10.8451 -0.0899676 10.6059 0.00581423 9.83761C0.0557033 9.43745 0.603723 9.0282 1.64987 8.60987Z' fill='%23E9503E'/%3E%3C/svg%3E ");
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }

  &-pagination {
    display: flex;

    @media (max-width: 480px) {
      width: 100%;
      justify-content: space-between;
    }

    &__btn {
      display: block;
      width: 60px;
      height: 60px;
      border-right: 1px solid var(--color-black);
      font-size: 18px;
      font-feature-settings: 'ss12' on;
      font-weight: 400;
      line-height: 25px;
      color: var(--color-black);

      &:first-child {
        border-left: 1px solid var(--color-black);

        @media (max-width: 480px) {
          border-left: none;
        }
      }

      &:not(:first-child) {
        @media (max-width: 480px) {
          border-left: 1px solid var(--color-black);
        }
      }

      &:last-child {
        @media (max-width: 480px) {
          border-right: none;
        }
      }

      &:hover {
        color: var(--color-red);
      }

      &.is-active {
        background: var(--color-red);
        color: #fff;
      }
    }
  }

  &__show-more {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 220px;
    border-left: 1px solid var(--color-black);

    @media (max-width: 480px) {
      display: none;
    }

    &_text {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }

    &_icon {
      background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 4.5H6.9C10.2603 4.5 11.9405 4.5 13.2239 5.15396C14.3529 5.7292 15.2708 6.64708 15.846 7.77606C16.5 9.05953 16.5 10.7397 16.5 14.1L16.5 20.5M16.5 20.5L12.5 16.5M16.5 20.5L20.5 16.5' stroke='%232A2A2A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
    }
  }

  &-empty {
    width: 80px;
    background-image: url(../resources/images/main-bg.png);
    border-left: 1px solid var(--color-black);

    &_bg {
      border-top: 1px solid var(--color-black);
      background: var(--color-white);
    }

    @media (max-width: 480px) {
      display: none;
    }
  }
}