.stories-page-hero {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    position: relative;
    padding: 90px 60px 60px;

    @media (max-width: 480px) {
      padding: 20px 20px 65px;
    }
  }

  &__inner {
    max-width: 1380px;

    @media (max-width: 480px) {
      max-width: none;
    }
  }

  &__author {
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: 500;
    line-height: 34px;
    color: var(--color-red);
    text-transform: uppercase;

    @media (max-width: 480px) {
      margin-bottom: 20px;
      font-size: 20px;
      line-height: 28px;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 50px;
    line-height: 55px;

    @media (max-width: 480px) {
      margin-bottom: 20px;
      font-size: 26px;
      line-height: 28px;
    }
  }

  &__descr {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: var(--color-black);

    @media (max-width: 480px) {
      font-size: 16px;
      line-height: 22px;
    }
  }
}