.object-gallery {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    position: relative;
    padding: 60px 0 60px 60px;

    @media (max-width: 480px) {
      padding: 40px 20px 50px;
    }
  }

  &__title {
    margin-bottom: 40px;
    font-size: 35px;
    line-height: 42px;

    @media (max-width: 480px) {
      max-width: 300px;
      font-size: 26px;
      line-height: 31px;
    }
  }

  &-swiper {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 360px;
      height: 100%;
      background: linear-gradient(90deg, #EDE9E0 0%, rgba(237, 233, 224, 0) 100%);
      transform: rotate(-180deg);
      z-index: 10;

      @media (max-width: 480px) {
        width: 150px;
      }
    }

    &.is-last {
      &::before {
        transform: none;
        right: initial;
        left: 0;
      }
    }

    &__button {
      top: 55%;
      transform: translateY(-50%);

      @media (max-width: 480px) {
        top: 60%;
      }

      &_prev {
        left: 80px;

        @media (max-width: 480px) {
          left: 30px;
        }
      }

      &_next {
        right: 80px;

        @media (max-width: 480px) {
          right: 30px;
        }
      }
    }
  }

  &-item {
    height: 280px;

    &_h100 {
      height: 100%;
    }

    img {
      height: 100%;

      @media (max-width: 480px) {
        width: 100%;
      }
    }

    &__img {
      height: 280px;

      &_video {
        position: relative;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: #0000008C;
          z-index: 1;
        }

        &::after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: 80px;
          height: 80px;
          background: #5975B8;
          background-image: url("data:image/svg+xml,%3Csvg width='25' height='29' viewBox='0 0 25 29' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M25 14.5L0.249999 28.7894L0.25 0.21058L25 14.5Z' fill='%23EDE9E0'/%3E%3C/svg%3E ");
          background-position: 30px center;
          background-repeat: no-repeat;
          border-radius: 100%;
          transform: translate(-50%, -50%);
          cursor: pointer;
          z-index: 2;
        }
      }
    }

    &_video {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #0000008C;
        z-index: 1;
      }

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 80px;
        height: 80px;
        background: #5975B8;
        background-image: url("data:image/svg+xml,%3Csvg width='25' height='29' viewBox='0 0 25 29' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M25 14.5L0.249999 28.7894L0.25 0.21058L25 14.5Z' fill='%23EDE9E0'/%3E%3C/svg%3E ");
        background-position: 30px center;
        background-repeat: no-repeat;
        border-radius: 100%;
        transform: translate(-50%, -50%);
        cursor: pointer;
        z-index: 2;
      }
    }

    &__name {
      margin-top: 15px;
      font-size: 20px;
      font-weight: 400;
      line-height: 28px;
      color: var(--color-black);
    }
  }
}