.organizations-main {
  border-bottom: 1px solid var(--color-black);
  
  &-wrapper {
    display: flex;
    padding: 60px 60px 70px 60px;

    @media (max-width: 480px) {
      display: block;
      padding: 20px 20px 60px;
    }
  }

  &-left {
    max-width: 300px;
    margin-right: 38px;

    @media (max-width: 480px) {
      max-width: none;
      margin: 0 0 40px 0;
    }
  }

  &-tabs {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    @media (max-width: 480px) {
      display: -webkit-box;
      gap: 0 20px;
      flex-wrap: nowrap;
      white-space: nowrap;
      overflow-x: scroll;
    }

    &__btn {
      position: relative;
      font-feature-settings: "ss12" on;
      padding: 21px 50px 17px 40px;

      &_active {
        background: var(--color-red);
        border: 1px solid var(--color-red);
        color: var(--color-white);

        .organizations-main-tabs__btn_count {
          color: var(--color-white);
        }
      }

      &_count {
        position: absolute;
        top: 17px;
        right: 40px;
        font-size: 13px;
        font-weight: 500;
        font-feature-settings: "ss12" on;
        color: var(--color-red);

        &_big {
          right: 36px;
        }
      }
    }
  }

  &-content {
    max-width: 1040px;
    width: 100%;

    @media (max-width: 480px) {
      max-width: none;
    }

    &__list {
      display: none;
      flex-wrap: wrap;
      gap: 20px 40px;

      &_active {
        display: flex;
      }
    }
  }

  &-item {
    max-width: 320px;

    @media (max-width: 480px) {
      margin: 0 auto;
    }

    &__header {
      margin-bottom: 16px;
    }

    &__title {
      margin-bottom: 5px;
      font-size: 20px;
      font-weight: 700;
      line-height: 24px;
      color: var(--color-black);
    }

    &__descr {
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      color: var(--color-black);
    }
  }
}