.organizations-page-about {
  border-bottom: 1px solid var(--color-black);

  &-inner {
    padding: 60px 0 60px 60px;

    @media (max-width: 480px) {
      padding: 40px 20px;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 35px;
    line-height: 42px;

    @media (max-width: 480px) {
      margin-bottom: 25px;
      font-size: 26px;
      line-height: 31px;
    }
  }

  &-list {
    display: flex;
    gap: 0 20px;

    @media (max-width: 480px) {
      flex-direction: column;
      gap: 30px 0;
    }
  }

  &-item {
    max-width: 320px;

    &_red {
      .organizations-page-about-item__heading {
        color: var(--color-red);
      }
      .organizations-page-about-item__subheading {
        color: var(--color-red);
      }
    }

    &_blue {
      .organizations-page-about-item__heading {
        color: var(--color-blue);
      }
      .organizations-page-about-item__subheading {
        color: var(--color-blue);
      }
    }

    &_violet {
      .organizations-page-about-item__heading {
        color: var(--color-violet);
      }
      .organizations-page-about-item__subheading {
        color: var(--color-violet);
      }
    }

    &__heading {
      margin-bottom: 8px;
      font-size: 70px;
      font-weight: 700;
      line-height: 70px;

      @media (max-width: 480px) {
        margin-bottom: 0;
        font-size: 50px;
        line-height: 50px;
      }
    }

    &__subheading {
      margin-bottom: 20px;
      font-size: 24px;
      font-weight: 700;
      line-height: 29px;

      @media (max-width: 480px) {
        font-size: 24px;
        line-height: 28px;
      }
    }

    &__descr {
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      color: var(--color-black);
    }
  }
}