.project-activities {
  background-image: url(../resources/images/main-bg.png);
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    display: flex;

    @media (max-width: 480px) {
      flex-direction: column;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 50px;
    line-height: 55px;

    @media (max-width: 480px) {
      max-width: 300px;
      font-size: 26px;
      line-height: 28px;
    }
  }

  &__descr {
    margin-bottom: 60px;
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: var(--color-black);

    @media (max-width: 480px) {
      margin-bottom: 20px;
      line-height: 22px;
    }
  }

  &-left {
    position: relative;
    padding: 90px 37px 94px 60px;

    @media (max-width: 480px) {
      padding: 20px 20px 35px;
    }

    &__inner {
      max-width: 720px;

      @media (max-width: 480px) {
        max-width: none;
      }
    }
  }

  &-contribution {
    &__count {
      display: block;
      margin-bottom: 15px;
      font-size: 64px;
      font-weight: 700;
      line-height: 64px;
      color: var(--color-red);

      @media (max-width: 480px) {
        margin-bottom: 5px;
        font-size: 48px;
        line-height: 48px;
      }
    }

    &__descr {
      font-size: 16px;
      font-weight: 700;
      line-height: 16px;
      color: var(--color-black);
      text-transform: uppercase;
    }
  }

  &-right {
    &__inner {
      padding: 40px 60px 40px 37px;

      @media (max-width: 480px) {
        padding: 0 20px 40px;
      }
    }
  }
}