.object-reservation {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    padding: 60px 140px 60px 60px;

    @media (max-width: 480px) {
      padding: 40px 20px 60px;
    }
  }

  &__title {
    margin-bottom: 46px;
    font-size: 35px;
    line-height: 42px;

    @media (max-width: 480px) {
      max-width: 300px;
      font-size: 26px;
      line-height: 31px;
    }
  }
}