.navbar {
  border-bottom: 1px solid var(--color-black);

  @media (max-width: 1499.98px) {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 360px;
    width: 100%;
    height: 100%;
    display: none;
    background: var(--color-white);
    border-right: 1px solid var(--color-black);
    border-bottom: none;
    transition: all .3s ease-in-out;
    z-index: 15;

    @media (max-width: 500px) {
      max-width: none;
    }

    &.is-active {
      display: block;
      animation: smoothVisible .3s ease-in-out;
    }

    .container {
      width: 100%;
      height: 100%;
    }
  }

  &-mobile {
    display: none;
    background-image: url(../resources/images/main-bg.png);

    @media (max-width: 1499.98px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px;
      border-bottom: 1px solid var(--color-black);
    }

    &__logo {
      display: block;

      img {
        max-width: 120px;
        height: 38px;
      }
    }

    &__phone {
      font-size: 15px;
      font-weight: 500;
      line-height: 19px;
      color: var(--color-black);
    }

    &__close {
      position: relative;
      width: 38px;
      height: 38px;
      cursor: pointer;

      &::before, &::after {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: var(--color-black);
      }

      &::before {
        top: 18px;
        transform: rotate(45deg);
      }

      &::after {
        top: 18px;
        transform: rotate(-45deg);
      }
    }
  }

  &-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 60px;

    @media (max-width: 1499.98px) {
      height: calc(100% - 82px);
      flex-direction: column;
      align-items: flex-start;
      padding-right: 0;
    }
  }

  &-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-right: 1px solid var(--color-black);

    &.hide {
      display: none;
    }

    &_two {
      display: none;

      &.is-open {
        display: block;
      }
    }

    @media (max-width: 1499.98px) {
      width: 100%;
      flex-direction: column;
      border-right: none;
      padding: 20px 20px 0 20px;
    }
  }

  &-item {
    width: 160px;
    margin: 0 auto;
    text-align: center;
    padding: 24px 0;

    @media (max-width: 1499.98px) {
      width: 100%;
      margin: 0;
      padding: 20px 0;
      text-align: left;
    }

    @media (max-width: 720px) {
      padding: 15px 0;
    }

    &_select {
      position: relative;

      &:hover {
        .navbar-select__body {
          display: block;
        }

        @media (max-width: 1499.98px) {
          .navbar-select__body {
            display: none;
          }
        }
      }
    }

    &__back {
      position: relative;
      padding-left: 34px;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;


      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 24px;
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 12H18M6 12L11 7M6 12L11 17' stroke='%23E9503E' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
        background-repeat: no-repeat;
      }
    }

    &__heading {
      font-size: 18px;
      font-weight: 500;
      line-height: 18px;
      color: var(--color-black);
      text-transform: uppercase;
    }

    &_pd-small {
      padding: 10px 0;
    }

    &_hidden {
      position: relative;
      padding-left: 34px;
      display: none;

      @media (max-width: 1499.98px) {
        display: block;
      }
    }

    &__link {
      display: block;
      font-size: 16px;
      font-weight: 400;
      line-height: 16px;
      color: var(--color-black);

      @media (max-width: 1499.98px) {
        br {
          display: none;
        }
      }

      &_search {
        &.is-open {
          .form__search {
            display: block;
            height: 24px;
            padding-left: 0;
          }

          .navbar-item__link_text {
            display: none;
          }
        }
      }

      &_open-icon {
        position: absolute;
        top: 10px;
        left: 0;
        width: 24px;
        height: 24px;
      }
    }
  }

  &-social {
    display: flex;
    align-items: center;

    @media (max-width: 1499.98px) {
      width: 100%;
      justify-content: flex-start;
      border-top: 1px solid var(--color-black);
    }

    @media (max-width: 500px) {
      max-width: 360px;
      width: 100%;
      border: 1px solid var(--color-black);
      margin-left: 0;
    }

    @media (max-width: 360.98px) {
      border-right: none;
      border-left: none;
    }

    &__link {
      position: relative;
      display: block;
      font-size: 16px;
      font-weight: 400;
      line-height: 16px;
      color: var(--color-black);


      &_vk {
        margin-right: 34px;
        padding-left: 44px;

        @media (max-width: 1499.98px) {
          margin-right: 0;
          padding: 20px 29px 20px 73px;
          border-right: 1px solid var(--color-black);
        }

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 29px;
          height: 19px;
          background-image: url("data:image/svg+xml,%3Csvg width='29' height='19' viewBox='0 0 29 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.795 19C5.88686 19 0.235478 11.8679 0 0H4.96315C5.12617 8.71071 8.78504 12.4004 11.6832 13.1612V0H16.3567V7.51251C19.2187 7.18919 22.2252 3.76577 23.2396 0H27.913C27.1341 4.64064 23.8737 8.06406 21.5551 9.47147C23.8737 10.6126 27.5871 13.5986 29 19H23.8555C22.7506 15.3864 19.9976 12.5906 16.3567 12.2102V19H15.795Z' fill='%23E9503E'/%3E%3C/svg%3E ");
          background-repeat: no-repeat;

          @media (max-width: 1499.98px) {
            top: 19px;
            left: 29px;
          }
        }
      }

      &_telegram {
        padding-left: 40px;

        @media (max-width: 1499.98px) {
          padding: 20px 32px 20px 75px;
        }

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 24px;
          height: 20px;
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='20' viewBox='0 0 24 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.64987 8.60987C8.09231 5.78741 12.3883 3.92667 14.5378 3.02765C20.675 0.460774 21.9503 0.0148807 22.7815 0.000156987C22.9643 -0.00308132 23.3731 0.0424773 23.6379 0.25852C23.8614 0.440943 23.9229 0.687369 23.9524 0.860326C23.9818 1.03328 24.0185 1.42728 23.9893 1.73514C23.6568 5.249 22.2177 13.7762 21.4856 17.7118C21.1758 19.3771 20.5658 19.9354 19.9753 19.9901C18.6919 20.1088 17.7174 19.1372 16.4744 18.3179C14.5294 17.0358 13.4305 16.2377 11.5425 14.9867C9.36064 13.5408 10.7751 12.7462 12.0185 11.4475C12.344 11.1076 17.9985 5.93584 18.1079 5.46668C18.1216 5.40801 18.1343 5.18929 18.0051 5.0738C17.8759 4.95831 17.6851 4.9978 17.5475 5.02921C17.3524 5.07373 14.2452 7.1389 8.22584 11.2247C7.34386 11.8337 6.545 12.1304 5.82924 12.1149C5.04018 12.0977 3.52233 11.6663 2.39397 11.2974C1.00999 10.8451 -0.0899676 10.6059 0.00581423 9.83761C0.0557033 9.43745 0.603723 9.0282 1.64987 8.60987Z' fill='%23E9503E'/%3E%3C/svg%3E ");
          background-repeat: no-repeat;

          @media (max-width: 1499.98px) {
            top: 19px;
            left: 35px;
          }
        }
      }
    }
  }

  &-select {
    &__header {
      cursor: pointer;
    }

    &__selected {
      font-size: 16px;
      font-weight: 400;
      line-height: 16px;
      color: var(--color-black);

      @media (max-width: 1499.98px) {
        br {
          display: none;
        }
      }
    }

    &__body {
      position: absolute;
      bottom: -120px;
      left: 0;
      display: none;
      width: 270px;
      height: 120px;
      background: var(--color-white);
      z-index: 10;

      &.is-open {
        display: block;
      }
    }

    &__list {
      border: 1px solid var(--color-black);
    }

    &__item {
      padding: 20px;
      text-align: left;

      &:not(:last-child) {
        border-bottom: 1px solid var(--color-black);
      }
    }

    &__link {
      font-size: 16px;
      font-weight: 400;
      line-height: 16px;
      color: var(--color-black);
    }
  }
}

@keyframes smoothVisible {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}