.object-peculiarities {
  border-bottom: 1px solid var(--color-black);
  
  &__inner {
    max-width: 1080px;
    width: 100%;
    padding: 60px 0 60px 60px;

    @media (max-width: 480px) {
      max-width: none;
      padding: 40px 20px;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-size: 35px;
    line-height: 42px;

    @media (max-width: 480px) {
      font-size: 26px;
      line-height: 31px;
    }
  }

  &__descr {
    margin-bottom: 38px;
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: var(--color-black);

    @media (max-width: 480px) {
      margin-bottom: 18px;
    }
  }

  &-list {
    display: flex;
    align-items: center;

    @media (max-width: 480px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &-item {
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-right: 44px;

      @media (max-width: 480px) {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }

    &__icon {
      display: block;
      margin-right: 10px;
    }

    &__text {
      font-size: 18px;
      font-weight: 400;
      line-height: 25px;
      color: var(--color-black);
    }
  }
}