.contacts-feedback {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    padding: 60px;

    @media (max-width: 480px) {
      padding: 40px 20px 60px;
    }
  }

  &__title {
    margin-bottom: 40px;
    font-size: 35px;
    line-height: 42px;

    @media (max-width: 480px) {
      font-size: 26px;
      line-height: 31px;
    }
  }

  &__form {
    max-width: 860px;
  }
}