.developing {
  background-image: url(../resources/images/main-bg.png);
  border-bottom: 1px solid var(--color-black);
  
  &-wrapper {
    position: relative;
    display: flex;
    padding: 70px 0;
  }

  &-left {
    margin-right: 110px;

    &__inner {
      padding: 0 55px 0 60px;

      img {
        margin-left: 200px;
      }
    }
  }

  &-right {
    max-width: 410px;
  }

  &__title {
    margin-bottom: 15px;
    font-size: 50px;
    line-height: 55px;
  }

  &__descr {
    margin-bottom: 35px;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.01em;
    color: var(--color-black);
  }

  &__btn {
    max-width: 240px;
    padding: 18px 0 21px;
    justify-content: center;
  }
}