.stories-main {
  border-bottom: 1px solid var(--color-black);

  &-wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__inner {
    padding: 60px 0 60px 60px;

    @media (max-width: 480px) {
      padding: 40px 20px 60px;
    }

    &_pagination {
      width: 95%;
      display: flex;
      justify-content: space-between;
      padding: 0 0 0 60px;
      border-top: 1px solid var(--color-black);

      @media (max-width: 480px) {
        width: 100%;
        padding: 0;
      }
    }
  }

  &-list {
    max-width: 1300px;
    display: flex;
    flex-wrap: wrap;
    gap: 40px 20px;

    @media (max-width: 480px) {
      max-width: none;
      justify-content: center;
      gap: 30px 0;
    }
  }

  &-item {
    max-width: 420px;
    width: 100%;
    display: flex;
    flex-direction: column;

    @media (max-width: 480px) {
      max-width: 320px;
    }

    &_left {
      .stories-main-item__header {
        img {
          transform: rotate(-2.22deg)
        }
      }
    }

    &_middle {
      .stories-main-item__header {
        img {
          transform: rotate(1.95deg)
        }
      }
    }

    &_right {
      .stories-main-item__header {
        img {
          transform: rotate(-2.48deg)
        }
      }
    }

    &__header {
      margin-bottom: 20px;
    }

    &__body {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &__name {
      margin-bottom: 10px;
      font-size: 18px;
      font-weight: 500;
      line-height: 25px;
      color: var(--color-red);
      text-transform: uppercase;
    }

    &__title {
      margin-bottom: 15px;
      font-size: 22px;
      font-weight: 700;
      line-height: 31px;
      color: var(--color-black);
      text-transform: uppercase;
    }

    &__btn {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }
  }

  &-empty {
    width: 80px;
    background-image: url(../resources/images/main-bg.png);
    border-left: 1px solid var(--color-black);

    @media (max-width: 480px) {
      display: none;
    }

    &_bg {
      border-top: 1px solid var(--color-black);
      background: var(--color-white);
    }
  }

  &-pagination {
    display: flex;

    @media (max-width: 480px) {
      width: 100%;
      justify-content: space-between;
    }

    &__btn {
      display: block;
      width: 60px;
      height: 60px;
      border-right: 1px solid var(--color-black);
      font-size: 18px;
      font-feature-settings: 'ss12' on;
      font-weight: 400;
      line-height: 25px;
      color: var(--color-black);

      &:first-child {
        border-left: 1px solid var(--color-black);

        @media (max-width: 480px) {
          border-left: none;
        }
      }

      &:not(:first-child) {
        @media (max-width: 480px) {
          border-left: 1px solid var(--color-black);
        }
      }

      &:hover {
        color: var(--color-red);
      }

      &.is-active {
        background: var(--color-red);
        color: #fff;
      }
    }
  }

  &__show-more {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 220px;
    border-left: 1px solid var(--color-black);

    @media (max-width: 480px) {
      display: none;
    }

    &_text {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      color: var(--color-black);
    }

    &_icon {
      background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 4.5H6.9C10.2603 4.5 11.9405 4.5 13.2239 5.15396C14.3529 5.7292 15.2708 6.64708 15.846 7.77606C16.5 9.05953 16.5 10.7397 16.5 14.1L16.5 20.5M16.5 20.5L12.5 16.5M16.5 20.5L20.5 16.5' stroke='%232A2A2A' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E ");
    }
  }
}