.contacts {
  background-image: url(../resources/images/main-bg.png);
  border-bottom: 1px solid var(--color-black);
  
  &-inner {
    position: relative;
    padding: 90px 60px 70px;

    @media (max-width: 480px) {
      padding: 20px 20px 60px;
    }
  }

  &-hero {
    display: flex;
    align-items: center;

    &__logo {
      margin-left: 40px;
    }
  }

  &__title {
    font-size: 50px;
    line-height: 55px;

    @media (max-width: 480px)  {
      font-size: 26px;
      line-height: 28px;
    }
  }
}