.project-page-about {
  border-bottom: 1px solid var(--color-black);

  &__buttons {
    display: flex;
    border-bottom: 1px solid var(--color-black);

    @media (max-width: 480px) {
      flex-direction: column;
    }
  }

  &__btn {
    width: 50%;
    padding: 16px 0;
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    text-transform: uppercase;
    transition: color .3s ease-in-out;

    @media (max-width: 480px) {
      width: 100%;
    }

    &:first-child {
      border-right: 1px solid var(--color-black);

      @media (max-width: 480px) {
        border-right: none;
        border-bottom: 1px solid var(--color-black);
      }
    }

    &_active {
      background-image: url(../resources/images/main-bg.png);

      &:hover {
        color: var(--color-black)!important;
      }
    }

    &:hover {
      color: var(--color-red);
    }
  }

  &__item {
    display: none;

    &_active {
      display: block;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid var(--color-black);

    @media (max-width: 480px) {
      flex-direction: column;
      padding: 30px 20px 40px;
    }
  }

  &__inner {
    padding: 60px 0 60px 60px;

    @media (max-width: 480px) {
      padding: 0;
      order: 2;
    }
  }

  &__info {
    max-width: 800px;

    @media (max-width: 480px) {
      max-width: none;
    }

    &:first-child {
      margin-bottom: 60px;
    }
  }

  &-summa {
    margin: 35px 0 30px;

    &__text {
      margin-bottom: 15px;
      font-size: 16px;
      font-weight: 700;
      line-height: 16px;
      color: var(--color-black);
      text-transform: uppercase;
    }

    &__count {
      font-size: 64px;
      font-weight: 700;
      line-height: 64px;
      color: var(--color-red);

      @media (max-width: 480px) {
        font-size: 48px;
        line-height: 48px;
      }
    }
  }

  &__title {
    margin-bottom: 20px;
    font-size: 35px;
    font-weight: 700;
    line-height: 42px;
    color: var(--color-black);
    text-transform: uppercase;

    @media (max-width: 480px) {
      font-size: 26px;
      line-height: 31px;
    }

    &_small {
      margin-bottom: 10px;
      font-size: 22px;
      font-weight: 700;
      line-height: 31px;
      text-transform: none;
    }
  }

  &__descr {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: var(--color-black);
  }

  &__image {
    margin-top: 60px;

    @media (max-width: 480px) {
      margin: 0 0 40px 0;
      margin-bottom: 40px;
      order: 1;
    }

    img {
      transform: rotate(3.44deg);
    }
  }

  &__empty {
    width: 80px;
    background-image: url(../resources/images/main-bg.png);
    border-left: 1px solid var(--color-black);

    @media (max-width: 480px) {
      display: none;
    }
  }
}